import * as THREE from 'three'

export default class Land
{
    constructor(color, size)
    {

        //섬의 외접원 크기
        this.size = size;

        //섬의 높이
        this.height = 1.;
        
        //랜덤 색상
        this.randomColor = 0.05;

        
        this.color = color

        this.setGeometry()
        this.setMaterial()
        this.setMesh()
        
        //레이캐스터용 type
        this.mesh.type = 'land'
    }

    setGeometry()
    {
        this.geometry = new THREE.CylinderGeometry(this.size, this.size, this.height, 6)
    }

    randomizeColor(color)
    {
        const rgb = new THREE.Color(color);
        const r = rgb.r + (Math.random() * this.randomColor*0);
        const g = rgb.g + (Math.random() * this.randomColor);
        const b = rgb.b + (Math.random() * this.randomColor*0.2);
        rgb.r = r
        rgb.g = g
        rgb.b = b
        return rgb;
    }
    
    setMaterial()
    {
        this.material = new THREE.MeshPhysicalMaterial({
            color: this.randomizeColor(this.color).convertSRGBToLinear().multiplyScalar(3.0),
            flatShading: true
        })
    }
    setMesh()
    {
        this.mesh = new THREE.Mesh(this.geometry, this.material)
        this.mesh.castShadow = true
    }
    setColor(color) {
        this.color = color;
        this.material.color.set(this.color).convertSRGBToLinear().multiplyScalar(3.5);
    }
}