import { CSS2DRenderer, CSS2DObject } from "three/examples/jsm/Addons.js";

import Experience from '../../Experience'

export default class TwodRender
{
    constructor(position, img, id, data)
    {
        this.experience = new Experience()
        this.sizes = this.experience.sizes
        this.scene = this.experience.scene
        this.camera = this.experience.camera.instance
        this.time = this.experience.time

        this.position = position
        this.img = img
        this.id = id

        this.data = data

        this.setInstance()
    }
    setInstance()
    {
        this.instance = new CSS2DRenderer();
        this.instance.setSize(this.sizes.width, this.sizes.height);
        this.instance.domElement.style.position =  'absolute';
        this.instance.domElement.style.top =  '0px';
        this.instance.domElement.style.pointerEvents =  'none';
        document.body.appendChild(this.instance.domElement)


        const div = document.createElement('div');
        div.className = 'index'
        div.setAttribute('data-index', this.id);

        const profileArea = document.createElement('div');
        profileArea.className = 'profileArea'

        //이름
        const name = document.createElement('div');
        name.textContent = this.data.name;
        name.className = 'name'
        profileArea.appendChild(name)

        //자기소개
        const context = document.createElement('div');
        context.textContent = this.data.context;
        context.className = 'context'
        profileArea.appendChild(context)

        //버튼
        const button = document.createElement('div');
        button.textContent = this.data.button;
        if(this.id === 0){
            button.className = 'profileButton'
        }else{
            button.className = 'profileButton blueButton'
        }
        profileArea.appendChild(button)


        const innerElement = document.createElement('div');
        innerElement.className = 'profile'
        innerElement.textContent = '';

        const profileImage = document.createElement('img');
        profileImage.className = 'profileImage'
        profileImage.src = this.img


        div.appendChild(profileArea)
        innerElement.appendChild(profileImage)
        div.appendChild(innerElement);

        const cPointLabel = new CSS2DObject(div);
        this.scene.add(cPointLabel);
        cPointLabel.position.x = this.position[0]
        cPointLabel.position.y = this.position[1]
        cPointLabel.position.z = this.position[2]
    }

    resize()
    {
        this.instance.setSize(this.sizes.width, this.sizes.height);
    }

    update()
    {
        this.instance.render(this.scene, this.camera)
    }
}