const Button = document.querySelector('.Button')
const BackArrow = Button.querySelector('img');
const UI = document.querySelector('.UI')

const Dimmed = document.querySelector('.Dimmed')

const touchArea = document.querySelector('.touchArea')

//상태 표시
let modal = true;

    Button.addEventListener('click',function(){
        if(modal){
            modal = false
            if(window.innerWidth >= 800){
                UI.classList.add('positionLeft')
                BackArrow.classList.remove('rotation')
            }else if(window.innerWidth < 800){
                Dimmed.classList.add('displayNone')
                UI.classList.add('positionDown')
            }
        }else if(!modal){
            modal = true
            if(window.innerWidth >= 800){
                UI.classList.remove('positionLeft')
                BackArrow.classList.add('rotation')
            }else if(window.innerWidth < 800){
                Dimmed.classList.remove('displayNone')
                UI.classList.remove('positionDown')
            }
        }
    })

Dimmed.addEventListener('click',function(){
    if(modal){
        modal = false
        Dimmed.classList.add('displayNone')
        UI.classList.add('positionDown')
    }else if(!modal){
        modal = true
        UI.classList.remove('positionDown')
    }
})

touchArea.addEventListener('click',function(){
    console.log('?')
    if(modal){
        modal = false
        Dimmed.classList.add('displayNone')
        UI.classList.add('positionDown')
    }else if(!modal){
        modal = true
        Dimmed.classList.remove('displayNone')
        UI.classList.remove('positionDown')
    }
})


