import * as THREE from 'three'

import Experience from "../Experience.js"
import EventEmitter from "../Utils/EventEmitter.js"

import RayCaster from '../Utils/rayCaster.js'


export default class DragCamera extends EventEmitter
{
    constructor()
    {
        super();
        this.experience = new Experience()
        this.canvas = this.experience.canvas

        this.cameraPosition = this.experience.camera.cameraPosition;
        this.cameraPositionCopy = new THREE.Vector3().copy(this.experience.camera.cameraPosition);

        //마우스 드래그 유무
        this.mouseIsdown = false;
        this.mouseIsdrag = false;

        //마우스 드래그 속도
        this.dragSpeed = 0.04;

        this.prveOffset = null;
        this.currOffset = null;

        this.RayCaster = new RayCaster()


        this.canvas.addEventListener('touchstart', (e)=>{
            this.mouseIsdown = true;
            this.mouseIsdrag = false;
            const offset = e.target.getBoundingClientRect()
            const prevOffsetX = e.touches[0].clientX
            const prevOffsetY = e.touches[0].clientY
            if(this.mouseIsdown){
                this.dragMouse(prevOffsetX - offset.x,prevOffsetY - offset.y,this.mouseIsdrag)
            }
        })
        this.canvas.addEventListener('mousedown', (e) => {
            this.mouseIsdown = true;
            this.mouseIsdrag = false;
            const offset = e.target.getBoundingClientRect();
            const prevOffsetX = e.clientX;
            const prevOffsetY = e.clientY;
            if (this.mouseIsdown) {
                this.dragMouse(prevOffsetX - offset.x, prevOffsetY - offset.y, this.mouseIsdrag);
            }
        });

        this.canvas.addEventListener('touchmove', (e)=>{
            if(this.mouseIsdown){
                this.mouseIsdrag = true;
                const offset = e.target.getBoundingClientRect()
                this.dragMouse(e.touches[0].clientX - offset.x,e.touches[0].clientY - offset.y, this.mouseIsdrag)
                this.trigger('drag')
            }
        })
        this.canvas.addEventListener('mousemove', (e) => {
            if (this.mouseIsdown) {
                this.mouseIsdrag = true;
                const offset = e.target.getBoundingClientRect();
                this.dragMouse(e.clientX - offset.x, e.clientY - offset.y, this.mouseIsdrag);
                this.trigger('drag');
            }
        });


        this.canvas.addEventListener('touchend', ()=>{
            this.handleMouseUp();
        })
        this.canvas.addEventListener('mouseup', (e) => {
            if(this.mouseIsdrag === false) {
                this.RayCaster.actvie(e)
            }
            this.handleMouseUp();
        });
        
    }
    
    dragMouse(x,y, mouseIsdrag){
        if(!mouseIsdrag){
            this.prveOffset = [x,y];
        }else{
            this.currOffset = [x,y];
            const diffX = this.currOffset[0] - this.prveOffset[0];
            const diffY = this.currOffset[1] - this.prveOffset[1];
            this.cameraPositionCopy.x -= diffX * this.dragSpeed;
            this.cameraPositionCopy.z -= diffY * this.dragSpeed;
            this.prveOffset = [x,y];
        }
    }
    
    handleMouseUp() {
        this.mouseIsdown = false;
        this.mouseIsdrag = false;
        this.prveOffset = null;
        this.currOffset = null;
    }

    update()
    {
        this.RayCaster.update()
    }
}