import * as THREE from 'three'
import Experience from '../../Experience.js'

import Land from "./land.js"
import Wavesfoam from "./wavesfoam.js"
import landMark from './landMark.js'
import Forest from '../Forest/forest.js'
import Crystal from '../Crystal/Crystal.js'

import House from '../House/Hosue'

export default class Lands
{
    constructor(position, mainPosition, number)
    {
        this.setAnimation()
        this.experience = new Experience()
        this.scene = this.experience.scene
        this.position = position;

        //섬의 색상
        this.color = "#789d54"

        //섬의 외접원 크기
        this.size = 1;

        this.group = new THREE.Group();

        this.land = new Land(this.color, this.size);
        this.wavesfoam = new Wavesfoam(this.size)
        this.group.add(this.land.mesh);
        this.group.add(this.wavesfoam.mesh);

        this.group.position.x = position[0]
        this.group.position.z = position[1]

        this.group.mainPosition = mainPosition;
        this.group.number = number;

        this.scene.add(this.group);

        this.group.traverse((child) =>
        {
            if(child instanceof THREE.Mesh)
            {
                child.receiveShadow = true
            }
        })
    }

    setAnimation()
    {
        this.animation = {}
    }

    //섬을 간단하게 움직이는 함수
    move(x, y) {
        this.group.position.set(x,0,y);
    }
    
    //섬에 색칠함
    setColor(color){
        this.color = color;
        this.land.setColor(this.color);
    }

    //섬에 모델도형 올려놓음
    setModel(model){
        this.landMark = new landMark(this.size, this.position, "#FFD700")
        this.houseRed = new House(model)
        this.group.add(this.houseRed.model);
        this.group.add(this.landMark.mesh);
        this.group.add(this.landMark.mesh_yellow);
    }

    setMain(color){
        // this.crystal = new Crystal(color)
        if(color == 'my'){
            this.landMark = new landMark(this.size, this.position, "#3592cc")
        }else{
            this.landMark = new landMark(this.size, this.position, "#ff5454")
        }
        this.group.add(this.landMark.mesh);
        this.group.add(this.landMark.mesh_yellow);
        // this.group.add(this.crystal.mesh);
        // this.animation.group = this.crystal
    }

    setForest(){
        this.forest = new Forest()
        this.group.add(this.forest.mesh_1);
        this.group.add(this.forest.mesh_2);
        this.group.add(this.forest.mesh_3);
        this.group.add(this.forest.mesh_4);
        this.group.add(this.forest.mesh_5);
    }


    update()
    {
        // this.animation.group.update()
    }
}