import * as THREE from 'three'
import Experience from "./Experience.js"
import EventEmitter from "../Experience/Utils/EventEmitter.js"

import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'

export default class Camera extends EventEmitter
{
    constructor()
    {
        super();

        this.experience = new Experience()
        this.sizes = this.experience.sizes
        this.scene = this.experience.scene
        this.canvas = this.experience.canvas

        this.setInstance()

        //처음 카메라 
        this.cameraPosition = this.instance.position;
        this.experience = window.experience

    }

    setInstance()
    {
        this.instance = new THREE.PerspectiveCamera(50, this.sizes.width / this.sizes.height, 0.1, 100)
        this.instance.position.set(0, 20, 24)
        this.instance.rotation.x = -0.7;
        this.scene.add(this.instance)
    }
    
    resize()
    {
        this.instance.aspect = this.sizes.width / this.sizes.height
        this.instance.updateProjectionMatrix()
    }

    rePosition()
    {
    }

    update()
    {
        this.instance.position.lerp(this.experience.dragCamera.cameraPositionCopy
        , 0.1);
    }
}