import * as THREE from 'three'
import Experience from "../../Experience"

import CurveLine from './Line';

export default class Lines {
    constructor(number) {
        this.positions = [
            [
                [{x:0,y:0},{x:-10,y:-10}],
                [{x:0,y:0},{x:-2,y:-19}]
            ],
            [
                [{x:-10,y:-10},{x:-2,y:-19}]
            ],
            [
                [{x:-2,y:-19},{x:0,y:0}],
                [{x:-2,y:-19},{x:15,y:-8}]
            ],
            [
                [{x:-15,y:-21},{x:15,y:-8}],
                [{x:-15,y:-21},{x:0,y:0}]
            ],
            [
                [{x:-15,y:-21},{x:15,y:-8}]
            ]
        ]

        this.number = number;
        this.Line = [];

        for(let e = 0; e < this.positions.length; e ++){
            this.Line[e] = []
            for(let i = 0; i < this.positions[e].length; i ++){
                this.Line[e].push(new CurveLine(
                    [this.positions[e][i][0].x,this.positions[e][i][0].y],[this.positions[e][i][1].x,this.positions[e][i][1].y])
                )
            }
        }

        for(let e = 0; e < this.positions.length; e ++){
            for(let i = 0; i < this.Line[e].length; i++){
                this.Line[e][i].setVisible(false)
            }
        }

    }
    update(){
        for(let e = 0; e < this.positions.length; e ++){
            for(let i = 0; i < this.Line[e].length; i++){
                this.Line[e][i].update()
            }
        }
    }
    setVisible(number) {
        for(let e = 0; e < this.positions.length; e ++){
            for(let i = 0; i < this.Line[e].length; i++){
                this.Line[e][i].setVisible(false)
            }
        }

        for(let i = 0; i < this.Line[number].length; i++){
            this.Line[number][i].setVisible(true)
        }
    }
}
