import * as THREE from 'three'
import Experience from '../Experience'
import Sizes from './Sizes'
import Lines from '../World/Line/Lines'

//인적사항 데이터
import peopleData from "../data"

export default class RayCaster
{
    constructor()
    {
        this.experience = new Experience()
        this.scene = this.experience.scene
        this.time = this.experience.time
        this.sizes = this.experience.sizes
        this.camera = this.experience.camera.instance

        this.raycaster = new THREE.Raycaster()
        this.mouse = new THREE.Vector2()

        this.activeObejct = null;

        this.Lines = new Lines()
    }

    cameraMove(position)
    {
        this.experience.dragCamera.cameraPositionCopy.x = position[0] + 0
        this.experience.dragCamera.cameraPositionCopy.z = position[1] + 24
    }

    //클릭한 섬의 indexnumber를 가져와서 활성화
    actvie(e){
        this.mouse.x = e.clientX / this.sizes.width * 2 - 1
        this.mouse.y = -(e.clientY / this.sizes.height) * 2 + 1
        this.raycaster.setFromCamera(this.mouse, this.camera)
        this.intersects = this.raycaster.intersectObjects(this.scene.children)
        const index = document.querySelectorAll('.index')
        const profileDrop = document.querySelector('.profileDrop')
        const Button_2 = profileDrop.querySelector('.profileButton')

        //한번만 움직일 수 있도록
        for(const object of this.intersects){
                if(object.object.type === 'land'){
                    for(const value of index){
                        if(value.dataset.index == object.object.parent.number){
                            this.actvieReset(index)
                            this.changeData(object.object.parent.number)
                            this.Lines.setVisible(object.object.parent.number)

                            if(object.object.parent.number === 0){
                                profileDrop.classList.add('active')
                                Button_2.classList.remove('blueButton')
                            }else{
                                profileDrop.classList.add('active')
                                Button_2.classList.add('blueButton')
                            }
                            this.activeObejct = value;
                            value.classList.add('active')
                        }
                    }
                    this.cameraMove(object.object.parent.mainPosition)
            }
        }
    }

    //다른 오브젝트를 누르거나 할때, 리셋
    actvieReset(index){
        for(const value of index){
            value.classList.remove('active')
        }
    }

    //모바일에서 하단 바 내용 바꾸기
    changeData(index)
    {
        const profileDrop = document.querySelector('.profileDrop')
        const profileImg = profileDrop.querySelector('.profileImg')
        const name = profileDrop.querySelector('.name')
        const context = profileDrop.querySelector('.context')
        const profileButton = profileDrop.querySelector('.profileButton')

        profileImg.src = peopleData[index].img
        name.innerText = peopleData[index].name
        context.innerText = peopleData[index].context
        profileButton.innerText = peopleData[index].button
    }

    update()
    {
        if(this.Lines){
            this.Lines.update()
        }
    }
}