import * as THREE from 'three'

export default class Wavesfoam
{
    constructor(size)
    {
        this.bubble_size = size + 1.2;

        this.setGeometry()
        this.setMaterial()
        this.setMesh()
    }
    coordination_bubble_geo(number)
    {
        const x = (this.bubble_size)/2*Math.sin(Math.PI/3 * number)
        const y = (this.bubble_size)/2*Math.cos(Math.PI/3 * number)
        return [x,y]
    }
    setGeometry()
    {
        this.geometry = new THREE.BufferGeometry();

        const vertices = new Float32Array( [
            this.coordination_bubble_geo(1)[0], 0.01,  this.coordination_bubble_geo(1)[1],
            this.coordination_bubble_geo(2)[0], 0.01,  this.coordination_bubble_geo(2)[1],
            this.coordination_bubble_geo(3)[0], 0.01,  this.coordination_bubble_geo(3)[1],
            this.coordination_bubble_geo(4)[0], 0.01,  this.coordination_bubble_geo(4)[1],
            this.coordination_bubble_geo(5)[0], 0.01,  this.coordination_bubble_geo(5)[1],
            this.coordination_bubble_geo(6)[0], 0.01,  this.coordination_bubble_geo(6)[1],
        ] )

        this.geometry.setAttribute( 'position', new THREE.BufferAttribute( vertices, 3 ) );

        // 인덱스 배열을 설정하여 육각형의 면을 정의합니다.
        const indices = new Uint16Array([    
            0, 1, 2,
            0, 2, 3,
            0, 3, 4,
            0, 4, 5,
        ]);
        this.geometry.setIndex(new THREE.BufferAttribute(indices, 1));
    }
    setMaterial()
    {
        this.material = new THREE.MeshStandardMaterial({ 
            color: new THREE.Color("#ffffff").multiplyScalar(1.4),
            flatShading: true,
        });
    }
    setMesh()
    {
        this.mesh = new THREE.Mesh(this.geometry, this.material)
        this.mesh.receiveShadow = true
    }
}