import * as THREE from 'three'
import Experience from '../../Experience'

export default class 
{
    constructor()
    {
        this.experience = new Experience()
        this.scene = this.experience.scene

        this.setGeometry()
        this.setMaterial()
        this.setMesh()
    }

    randomizeColor(color)
    {
        const rgb = new THREE.Color(color);
        const r = rgb.r + (Math.random() * this.randomColor*0);
        const g = rgb.g + (Math.random() * this.randomColor);
        const b = rgb.b + (Math.random() * this.randomColor*0.2);
        rgb.r = r
        rgb.g = g
        rgb.b = b
        return rgb;
    }
    

    setGeometry()
    {
        this.geometry_1 = new THREE.CylinderGeometry(0, 0.3, 0.9, 4)
        this.geometry_2 = new THREE.CylinderGeometry(0, 0.34, 1.0, 4)
        this.geometry_3 = new THREE.CylinderGeometry(0, 0.45, 1.1, 4)
        this.geometry_4 = new THREE.CylinderGeometry(0, 0.35, 0.9, 4)
        this.geometry_5 = new THREE.CylinderGeometry(0, 0.16, 0.4, 4)
    }

    setMaterial()
    {
        this.material_1 = new THREE.MeshPhysicalMaterial({
        color: new THREE.Color("#628464").convertSRGBToLinear().multiplyScalar(3),
        flatShading: true
        })
        this.material_2 = new THREE.MeshPhysicalMaterial({
        color: new THREE.Color("#618765").convertSRGBToLinear().multiplyScalar(3),
        flatShading: true
        })
        this.material_3 = new THREE.MeshPhysicalMaterial({
        color: new THREE.Color("#638666").convertSRGBToLinear().multiplyScalar(3),
        flatShading: true
        })
        this.material_4 = new THREE.MeshPhysicalMaterial({
        color: new THREE.Color("#648867").convertSRGBToLinear().multiplyScalar(3),
        flatShading: true
        })
        this.material_5 = new THREE.MeshPhysicalMaterial({
        color: new THREE.Color("#668968").convertSRGBToLinear().multiplyScalar(3),
        flatShading: true
        })
    }
    setMesh()
    {
        this.mesh_1 = new THREE.Mesh(this.geometry_1, this.material_1)
        this.mesh_2 = new THREE.Mesh(this.geometry_2, this.material_2)
        this.mesh_3 = new THREE.Mesh(this.geometry_3, this.material_3)
        this.mesh_4 = new THREE.Mesh(this.geometry_4, this.material_4)
        this.mesh_5 = new THREE.Mesh(this.geometry_5, this.material_5)
        
        this.mesh_1.castShadow = true
        this.mesh_2.castShadow = true
        this.mesh_3.castShadow = true
        this.mesh_4.castShadow = true
        this.mesh_5.castShadow = true

        this.mesh_1.position.set(0.27,1,0.3)
        this.mesh_1.rotation.y = Math.PI/4

        this.mesh_2.position.set(-0.3,1,0.3)
        this.mesh_2.rotation.y = Math.PI/4

        this.mesh_3.position.set(-0.4,1,-0.3)
        this.mesh_3.rotation.y = Math.PI/4

        this.mesh_4.position.set(0.12,1,-0.5)
        this.mesh_4.rotation.y = Math.PI/4
        
        this.mesh_5.position.set(0.5,1,0.1)
        this.mesh_5.rotation.y = Math.PI/4
        
        this.scene.add(this.mesh_1)
        this.scene.add(this.mesh_2)
        this.scene.add(this.mesh_3)
        this.scene.add(this.mesh_4)
        this.scene.add(this.mesh_5)
    }
}