import * as THREE from 'three'
import Lands from './Lands'
import Converter from '../../Utils/Converter'
//섬 좌표
import * as coordinate from '../../coordinate.json'
import House from '../House/Hosue'


export default class LandArray
{
    constructor()
    {
        this.setAnimation()
    

        this.creatLand('My_Land', [0, 0], true, 'my', 0)
        this.creatLand('Land_one', [-10, -10], true, 'other', 1)
        this.creatLand('Land_two', [-2, -19], false, 'other', 2)
        this.creatLand('Land_three', [-15, -21], false, 'other', 3)
        this.creatLand('Land_four', [15, -8], true, 'other', 4)

    }
    setAnimation()
    {
        this.animation = []
    }

    creatLand(id, position, friend, my, number){
        for(const value of coordinate[id]){
            const x = new Converter(value.x, value.y, 1).x + position[0]
            const y = new Converter(value.x, value.y, 1).y + position[1]
            const group = new Lands([x,y], position, number)
            if(friend){
                if(value.p){
                    if(value.p === "main"){
                        group.setMain(my)
                    }else{
                        group.setModel(value.p)
                    }
                }else if(Math.random()>0.4){
                    group.setForest()
                    group.setColor('#60823e')
                }
            }else{
                group.setColor('#949693')
            }
           
        }
    }

    update()
    {
        for(let i = 0; i < this.animation.length; i ++){
            this.animation[i].update()
        }
    }
}