import TwodRender from "./2drender"
import Experience from "../../Experience"

//인적사항 데이터
import peopleData from "../../data"

export default class TwodRenders
{
    constructor()
    {

        this.experience = new Experience()
        this.resources = this.experience.resources
        this.profileImage = this.resources.items.profileImage.path

        //인적사항 데이터
        this.profileData = peopleData
        this.TwodRender = new TwodRender([0,2.1,0], this.profileImage[0], 0, this.profileData[0])
        this.TwodRender = new TwodRender([-10,2.1,-10], this.profileImage[1], 1, this.profileData[1])
        this.TwodRender = new TwodRender([-2,2.1,-19], this.profileImage[3], 2, this.profileData[2])
        this.TwodRender = new TwodRender([-15,2.1,-21], this.profileImage[3], 3, this.profileData[3])
        this.TwodRender = new TwodRender([15,2.1,-8], this.profileImage[2], 4, this.profileData[4])

    }
    resize()
    {
        this.TwodRender.resize()
    }
    update()
    {
        this.TwodRender.update()
    }
}