import * as THREE from 'three'
import { MeshLine, MeshLineMaterial } from 'three.meshline';

import Experience from '../../Experience.js'
import { materialOpacity } from 'three/examples/jsm/nodes/Nodes.js';

export default class CurveLine {
    constructor(first,second) {
        this.experience = new Experience();
        this.scene = this.experience.scene;
        this.positions = [
            new THREE.Vector3(first[0], 1, first[1]),
            new THREE.Vector3((first[0] + second[0])*0.5, 3, (first[1] + second[1])*0.5),
            new THREE.Vector3(second[0], 1, second[1])]
        this.points = this.positions;
        this.offest = 0
        this.setGeometry();
        this.setMaterial();
        this.setMesh();
    }

    setGeometry() {
        // Define the points of the curve
        const curve = new THREE.CatmullRomCurve3(this.points);
        const points = curve.getPoints(100); // Generate points along the curve

        // Convert the points into a BufferGeometry
        const positions = new Float32Array(points.length * 3);
        points.forEach((point, index) => {
            positions[index * 3] = point.x;
            positions[index * 3 + 1] = point.y;
            positions[index * 3 + 2] = point.z;
        });
        this.geometry = new THREE.BufferGeometry();
        this.geometry.setAttribute('position', new THREE.BufferAttribute(positions, 3));
        
        // Create a MeshLine and set its geometry
        this.line = new MeshLine();
        this.line.setGeometry(this.geometry);
    }

    setMaterial() {
        this.material = new MeshLineMaterial({
            color: '#ffffff',
            lineWidth: 0.15,
            transparent: true,
            dashArray: 0.1,
            dashOffset: this.offest,
            dashRatio: 0.35,
        });
    }

    setMesh() {
        this.mesh = new THREE.Mesh(this.line, this.material);
        this.scene.add(this.mesh);
    }

    update(){
        this.material.dashOffset -= 0.001
    }
    
    setVisible(visible) {
        this.mesh.visible = visible;
    }
    
}
