import * as THREE from 'three'
import Experience from '../../Experience.js'

export default class LandMark
{
    constructor(size, position, color)
    {
        this.size = size
        this.experience = new Experience()
        this.position = position;
        this.color = color

        this.setGeometry()
        this.setMaterial()
        this.setMesh()
        this.setModel()

    }
    setGeometry()
    {
        //갈색 판
        this.geometry = new THREE.CylinderGeometry(this.size - 0.08, this.size - 0.08, 0.4, 6)

        //노란 판
        this.geometry_yellow = new THREE.CylinderGeometry(this.size - 0.2, this.size - 0.2, 0.5, 6)

    }
    setMaterial()
    {
        this.material = new THREE.MeshPhysicalMaterial({
            color: new THREE.Color("#36341d"),
            flatShading: true
        })
        
        this.material_yellow = new THREE.MeshPhysicalMaterial({
            color: new THREE.Color(this.color),
            flatShading: true
        })
    }
    setMesh()
    {
        this.mesh = new THREE.Mesh(this.geometry, this.material)
        this.mesh.castShadow = true
        this.mesh.position.y = 0.5
        this.mesh.receiveShadow = true

        this.mesh_yellow = new THREE.Mesh(this.geometry_yellow, this.material_yellow)
        this.mesh_yellow.castShadow = true
        this.mesh_yellow.position.y = 0.6
        this.mesh_yellow.receiveShadow = true

    }
    setModel()
    {
    }
}