import * as THREE from 'three'
import Experience from '../../Experience'

export default class House
{
    constructor(model)
    {
        this.experience = new Experience()
        this.scene = this.experience.scene
        this.resources = this.experience.resources
        this.time = this.experience.time
        this.debug = this.experience.debug

        // Resource
        if(model === "photoshop"){
            this.resource = this.resources.items.building_01
        }else if(model === "work"){
            this.resource = this.resources.items.building_03
        }else if(model === "illust"){
            this.resource = this.resources.items.building_02
        }else if(model === "interview"){
            this.resource = this.resources.items.house01Red
        }
        this.setModel()
    }

    setModel()
    {
        this.model = this.resource.scene.clone()
        this.model.scale.set(0.4, 0.4, 0.4)
        this.model.position.set(0,0.9,0)
        this.model.rotation.y = -Math.PI/2
        this.scene.add(this.model)

        this.model.traverse((child) =>
        {
            if(child instanceof THREE.Mesh)
            {
                child.castShadow = true
            }
        })
    }
}